.menu {
  display: flex;
  align-items: center;
  padding-top: 4px;

  @media screen and (max-width: $breakpoints) {
    padding-top: 0;
  }

  &__item {
    position: relative;
  }

  &__link {
    padding: 5px 0;
    font-size: 16px;
    display: inline-block;
  }

  &__link::after {
    background: #072C74;
    height: 2px;
  }

  &__link:hover::after {
    animation: menu 0.7s both ease-out;
  }

  &__link.active::after {
    transform: translate(0, 0);
  }
}

.drop-down {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding: 0 7px;

  &__arrow {
    position: relative;
    top: -2px;
  }

  &__content {
    position: absolute;
    top: 29px;
    left: 0;
    display: none;
    width: 100%;
    padding: 0 7px 10px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    background: rgba(255, 255, 255, 0.9);

    @media screen and (max-width: $breakpoints) {
      position: static;
      width: auto;
      background: none;
    }
  }

  &__content::before {
    content: '';
    position: absolute;
    top: -1px;
    left: 7px;
    width: calc(100% - 14px);
    display: block;
    height: 1px;
    background: rgba($color: #072C74, $alpha: 0.3);

    @media screen and (max-width: $breakpoints) {
      display: none;
    }
  }

  &.active &__arrow svg {
    transform: scale(1, -1);
  }

  &.active {
    background: rgba(255, 255, 255, 0.9);

    @media screen and (max-width: $breakpoints) {
      background: none;
    }
  }

}

// -----------------------------------
.header {
  background: rgba($color: #FFFFFF, $alpha: 0.9);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  display: block;

  @media screen and (max-width: $breakpoints) {
    display: none;
    height: 100vh;
    overflow-y: auto;
    background-color: #FFFFFF;
    background-image: url(../img/mob-menu.svg);
    background-repeat: no-repeat;
    background-position: center bottom;
  }

  &__content {
    height: 60px;
    display: flex;
    align-items: center;

    @media screen and (max-width: $breakpoints) {
      flex-direction: column;
      height: auto;
      padding-top: 75px;
      padding-bottom: 100px;
    }
  }

  &__nav {
    margin-left: auto;
    display: flex;
    align-items: center;

    @media screen and (max-width: $breakpoints) {
      flex-direction: column;
      margin-left: 0;
    }
  }

  &__menu {
    @media screen and (max-width: $breakpoints) {
      flex-direction: column;
      padding-bottom: 10px;
    }

    >li:not(:last-child) {
      margin-right: 34px;

      @media screen and (max-width: $breakpoints) {
        margin-right: 0;
      }
    }

    a {
      @media screen and (max-width: $breakpoints) {
        margin-bottom: 6px;
      }
    }
  }

  &__submenu {
    @media screen and (max-width: $breakpoints) {
      display: block;
      padding: 0;
    }
  }

  &__drop-down {
    text-align: right;

    @media screen and (max-width: $breakpoints) {
      text-align: center;
      padding: 15px 0 0;
    }

    >a {
      font-weight: bold;
    }

    .drop-down__arrow {
      svg {
        @media screen and (max-width: $breakpoints) {
          transform: scale(1, -1);
        }
      }

    }
  }

  &__button {
    cursor: pointer;
    display: block;
    margin-left: 30px;
    text-align: center;
    padding: 8px 20px 7px;
    font-size: 14px;
    line-height: 1;
    text-transform: uppercase;
    color: #FF6E4E;
    border: 2px solid #FF6E4E;
    border-radius: 20px;
    transition: 0.3s;

    @media screen and (max-width: $breakpoints) {
      order: 3;
      background: #FF6E4E;
      color: #FFFFFF;
      min-width: 144px;
      max-width: 100%;
      margin: 0 auto;
    }
  }

  &__button:hover {
    background: #FF6D4E;
    border: 2px solid #FF6D4E;
    color: #FFFFFF;
    transition: 0.3s;
  }

  &__button:active {
    background: #D74213;
    border: 2px solid #D74213;
    color: #FFFFFF;
    transition: 0.3s;

  }

  &__lang {
    margin-left: 50px;

    @media screen and (max-width: $breakpoints) {
      margin-left: 0;
      padding-bottom: 40px;
    }
  }

  &__logo {
    display: block;

    @media screen and (max-width: $breakpoints) {
      display: none;
    }

    img {
      width: 158px;
      height: 39px;
    }
  }
}

.lang {
  &__menu {
    .menu__link {
      @media screen and (max-width: $breakpoints) {
        margin: 0 10px;
        color: rgba($color: #072C74, $alpha: 0.5);
      }
    }

    .menu__link.lang-active {
      @media screen and (max-width: $breakpoints) {
        color: rgba($color: #072C74, $alpha: 1);
      }
    }
  }

  &__submenu {
    @media screen and (max-width: $breakpoints) {
      display: flex;
      padding: 0;
    }
  }

  &__drop-down {
    @media screen and (max-width: $breakpoints) {
      display: flex;
      padding: 0;
    }

    .menu__link::after {
      @media screen and (max-width: $breakpoints) {
        display: none;
      }
    }

    .drop-down__arrow {
      @media screen and (max-width: $breakpoints) {
        display: none;
      }
    }
  }
}

.header-mob {
  display: none;

  background: rgba(255, 255, 255, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1500;

  @media screen and (max-width: $breakpoints) {
    display: block;
  }

  &__content {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    height: 43px;
  }

  &__logo {
    padding-top: 8px;

    img {
      width: 150px;
      height: 37px;
      transform-origin: 0 0;
      transform: scale(1, 1);
      transition: 0.5s;
    }
  }

  &__logo.logo_min {
    img {
      // width: 109px;
      // height: 26px;
      transform: scale(0.75, 0.75);
      transform-origin: 0 0;
      transition: 0.5s;
    }
  }

  &__burger {
    padding-top: 12px;
  }
}

.burger {
  display: none;
  width: 22px;
  height: 20px;
  cursor: pointer;
  position: relative;

  @media screen and (max-width: $breakpoints) {
    display: block;
  }

  &__line {
    position: absolute;
    top: 0;
    width: 22px;
    height: 2px;
    background: #072C74;
    transition: 0.3s all;
    left: 0;
  }

  &__line_2,
  &__line_3 {
    margin-top: 10px;
  }

  &__line_4 {
    margin-top: 20px;
  }
}

.open.burger {
  .burger {
    &__line_1 {
      display: none;
    }

    &__line_2 {
      transform: rotate(45deg);
      background: #072C74;
    }

    &__line_3 {
      transform: rotate(-45deg);
      background: #072C74;
    }

    &__line_4 {
      display: none;
    }
  }
}