.footer {
  overflow: hidden;
  position: relative;
  background-color: #fff;

  &__content {
    padding-top: 132px;
    padding-bottom: 70px;
    position: relative;

    @media screen and (max-width: $breakpoints) {
      padding-top: 90px;
      padding-bottom: 155px;
    }
  }

  &__img {
    position: absolute;
    left: 65px;
    bottom: -55px;
    display: block;
    animation: move 1s linear infinite;
    width: 280px;
    height: 284px;

    @media screen and (max-width: $breakpoints) {
      left: -45px;
      bottom: -30px;
      width: 230px;
      height: 180px;
    }

  }

  &__bg {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
  }

  &__bg-line {
    display: flex;

    img {
      flex-shrink: 0;
      height: 229px;
      width: 1108px;
      display: block;
      animation: line 40s linear infinite;

      @media screen and (max-width: $breakpoints) {
        height: 160px;
        width: 774px;
      }
    }

  }

  &__logo {
    text-align: center;
    padding-bottom: 60px;

    @media screen and (max-width: $breakpoints) {
      padding-bottom: 40px;
    }

    img {
      width: 164px;
      height: 41px;

      @media screen and (max-width: $breakpoints) {}
    }
  }

  &__list {
    display: flex;
    justify-content: center;
    padding-bottom: 55px;

    @media screen and (max-width: $breakpoints) {
      display: block;
      padding-bottom: 45px;
      text-align: center;
    }

    li {
      padding: 0 20px;

      @media screen and (max-width: $breakpoints) {
        padding: 0;
      }
    }

    li:not(:last-child) {
      @media screen and (max-width: $breakpoints) {
        margin-bottom: 15px;
      }
    }

    a {
      font-size: 14px;
      text-decoration: underline;
    }
  }

  &__text {
    padding-bottom: 40px;
    width: 290px;
    margin: 0 auto;
    font-size: 12px;
    text-align: center;
    color: rgba($color: #072C74, $alpha: 0.4);

    @media screen and (max-width: $breakpoints) {
      width: 100%;
      padding-bottom: 35px;
    }
  }

  &__form-block {
    padding-bottom: 10px;
    width: 292px;
    margin: 0 auto;

    @media screen and (max-width: $breakpoints) {
      width: 100%;
    }
  }

  &__input.form__input {
    padding-right: 40px;

    @media screen and (max-width: $breakpoints) {}
  }

  &__button {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    right: 2px;
    cursor: pointer;
    flex-shrink: 0;
    width: 36px;
    height: 36px;
    background: #FF6E4E;
    border: none;
    border-radius: 50%;

    @media screen and (max-width: $breakpoints) {}

    div {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  &__button:hover {
    background: #FD8A65;
  }

  &__button:active {
    background: #D74213;
  }

}

.copyright {
  font-size: 14px;
  text-align: center;
  color: rgba($color: #072C74, $alpha: 0.5);
}