.section1 {
  overflow: hidden;
  background-color: #fff;
  &__img-block {
    margin: 0 auto;
    position: relative;

    height: 349px;

    @media screen and (max-width: $breakpoints) {
      height: 206px;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: 1495px;
    height: 349px;

    @media screen and (max-width: $breakpoints) {
      width: 885px;
      height: 206px;
      left: 0;
      transform: translate(-65px, 0);
    }
  }

  &__img_0 {
    @media screen and (max-width: $breakpoints) {
      transform: translate(-65px, 0);
      transition: 0.5s;
    }
  }

  &__img_1 {
    @media screen and (max-width: $breakpoints) {
      transform: translate(-275px, 0);
      transition: 0.5s;
    }
  }

  &__img_2 {
    @media screen and (max-width: $breakpoints) {
      transform: translate(-500px, 0);
      transition: 0.5s;
    }
  }

  &__bg {

    position: absolute;
    top: -10px;
    left: 0;
    height: 90px;
    display: flex;

    @media screen and (max-width: $breakpoints) {
      height: 53px;
    }

    img {
      flex-shrink: 0;
      width: 1195px;
      height: 90px;
      animation: line 30s linear infinite;

      @media screen and (max-width: $breakpoints) {
        width: 707px;
        height: 53px;
      }
    }
  }

  &__content {
    padding-top: 90px;

    @media screen and (max-width: $breakpoints) {
      padding-top: 55px;
    }
  }

  &__container {
    width: 1140px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 45px;
    margin: 0 auto;

    @media screen and (max-width: $breakpoints) {
      width: 100%;
      padding: 0;
    }
  }

  &__title {
    margin: 0 auto;
    width: 650px;
    padding-bottom: 60px;

    @media screen and (max-width: $breakpoints) {
      width: 100%;
      padding-bottom: 25px;
    }
  }

  &__sections {
    display: flex;
    height: 190px;
    overflow: hidden;

    @media screen and (max-width: $breakpoints) {
      height: auto;
      display: block;
      overflow: visible;
    }
  }

  &__section {
    cursor: pointer;
    flex: 0 0 33.3333%;
    max-width: 33.3333%;
    padding-left: 15px;
    padding-right: 15px;
    position: relative;
    background-color: #fff;

    @media screen and (max-width: $breakpoints) {
      flex: 0 0 100%;
      max-width: 100%;
    }
  }

  &__section-content {
    padding-top: 5px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    transform: translate(0, 148px);
    transition: 0.5s transform;

    @media screen and (max-width: $breakpoints) {
      transform: translate(0, 0);
      min-height: 220px;
      width: 320px;
      padding-left: 15px;
      padding-right: 15px;
      margin: 0 auto;
    }

    .text {
      opacity: 0;
      transition: 1s opacity;
      transform: translate(0, 50px);

      @media screen and (max-width: $breakpoints) {
        opacity: 1;
        transform: translate(0, 0);
      }

    }
  }

  &__section::after {
    content: '';
    width: calc(100% - 30px);
    height: 3px;
    position: absolute;
    bottom: 0;
    left: 15px;
    background: rgba($color: #072C74, $alpha: 0.4);
    transition: 0.5s background, 0.5s width;

    @media screen and (max-width: $breakpoints) {
      display: none;
    }
  }

  &__section.hover-active::after {
    background: rgba($color: #072C74, $alpha: 0);
  }

  &__section.hover-active &__section-content {
    transform: translate(0, 0);
    transition: 0.5s transform;

    .text {
      opacity: 1;
      transform: translate(0, 0);
      transition: 2s opacity, 1s transform;
    }
  }

  &__section.hover-active .link-hover::after {
    animation: 0.7s ease-out 0.5s both menu;
  }

  &__section.active::after {
    background: #FF6E4E;
    width: var(--link-width);
    transition: 0.5s background, 0.5s width;
  }

  &__section.active &__section-content {
    transform: translate(0, 0);
    transition: 0.5s transform;

    .text {
      opacity: 1;
      transform: translate(0, 0);
      transition: 2s opacity, 1s transform;
    }
  }

  &__section-title {
    font-size: 25px;
    padding-bottom: 5px;

    @media screen and (max-width: $breakpoints) {}
  }

  &__section-text {
    // padding-bottom: 5px;

  }

  &__section-link {
    color: #FF6E4E;
    text-transform: uppercase;
    display: inline-block;
    padding: 5px 0;
    line-height: 1.3;
  }

  &__section-link::after {
    background: #FF6E4E;
    height: 3px;

    @media screen and (max-width: $breakpoints) {
      transform: translate(0, 0);
    }
  }

  &__logo {
    padding-bottom: 60px;

    @media screen and (max-width: $breakpoints) {
      padding-bottom: 0;
      height: 200px;
      display: flex;
    }
  }

  &__logo-list {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media screen and (max-width: $breakpoints) {
      width: 600px;
      height: 100%;
      flex-shrink: 0;
      justify-content: space-around;
      animation: line 30s linear infinite;
    }

    li {
      width: 10%;
      flex-shrink: 0;

      @media screen and (max-width: $breakpoints) {
        width: 14%;
      }

      img {
        width: 100%;
      }
    }

    svg {
      width: 100%;
    }
  }

  &__logo-list_mob {
    display: none;

    @media screen and (max-width: $breakpoints) {
      display: flex;
    }
  }
}