.section2 {
  // display: flex;
  // height: 100%;
  // align-items: center;
  overflow: hidden;

  @media screen and (max-width: $breakpoints) {}

  &__content {}

  &__title {
    padding-bottom: 80px;

    @media screen and (max-width: $breakpoints) {
      padding-bottom: 45px;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media screen and (max-width: $breakpoints) {
      display: block;
    }
  }

  &__item-title {
    margin-bottom: 5px;
    // @media screen and (max-width: $breakpoints) {
    //   margin-bottom: 15px;
    // }
  }

  &__item-img {
    @media screen and (max-width: $breakpoints) {
      margin-bottom: 30px;
      position: relative;
    }
  }

  &__img {
    @media screen and (max-width: $breakpoints) {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
    }
  }

  &__item-img_1 {
    @media screen and (max-width: $breakpoints) {
      height: 165px;
    }
  }

  &__item-img_2 {
    @media screen and (max-width: $breakpoints) {
      height: 178px;
    }
  }

  &__item-img_3 {
    @media screen and (max-width: $breakpoints) {
      height: 206px;
    }
  }

  &__item-img_4 {
    @media screen and (max-width: $breakpoints) {
      height: 180px;
    }
  }

  &__item-img_5 {
    @media screen and (max-width: $breakpoints) {
      height: 189px;
    }
  }

  &__item-img_6 {
    @media screen and (max-width: $breakpoints) {
      height: 222px;
    }
  }

  &__img_1 {
    @media screen and (max-width: $breakpoints) {
      width: 332px;
      height: 165px;
    }
  }

  &__img_2 {
    @media screen and (max-width: $breakpoints) {
      width: 379px;
      height: 178px;
    }
  }

  &__img_3 {
    @media screen and (max-width: $breakpoints) {
      width: 410px;
      height: 206px;
    }
  }

  &__img_4 {
    @media screen and (max-width: $breakpoints) {
      width: 361px;
      height: 180px;
    }
  }

  &__img_5 {
    @media screen and (max-width: $breakpoints) {
      width: 331px;
      height: 189px;
    }
  }

  &__img_6 {
    @media screen and (max-width: $breakpoints) {
      width: 430px;
      height: 222px;
    }
  }


  &__item-text {
    flex-shrink: 0;
    width: 450px;

    @media screen and (max-width: $breakpoints) {
      width: 100%;
      padding-bottom: 50px;
    }
  }

  &__item_even {
    padding-top: 100px;

    @media screen and (max-width: $breakpoints) {
      padding-top: 0;
    }

    .section2__item-img {
      order: 2;
    }

  }
}