/* Dots */
.slick-dotted.slick-slider {
    padding-top: 50px;
}

.slick-dots {
    position: absolute;
    top: 5px;

    display: block;

    width: 100%;
    padding: 0;
    margin: 0;

    list-style: none;
    text-align: center;

    z-index: 200;
}

.slick-dots li {
    position: relative;

    display: inline-block;

    width: 20px;
    height: 20px;
    margin: 0 10px;
    padding: 0;

    cursor: pointer;
  
}

.slick-dots li button {
    font-size: 0;
    line-height: 0;

    display: block;

    width: 20px;
    height: 20px;
    padding: 0;

    cursor: pointer;

    color: rgba($color: #ffffff, $alpha: 0);
    border: 0;
    outline: none;
    background: rgba($color: #ffffff, $alpha: 0);
}

.slick-dots li button:hover,
.slick-dots li button:focus {
    outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
    opacity: 1;
}

.slick-dots li button:before {

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: url(../img/dots.svg) no-repeat center/100% 100%;
    width: 18px;
    height: 18px;
    // opacity: 0.7;
    content: '';
    
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
    // opacity: 1;
    width: 24px;
    height: 24px;
    background: url(../img/dots-active.svg) no-repeat center/100% 100%;
   
}


