.section3 {
  position: relative;
  overflow: hidden;

  @media screen and (max-width: $breakpoints) {
    height: 505px;
    background-image: url(../img/IMG_API-bg-mob.svg);
    background-repeat: no-repeat;
    background-position: center bottom;
  }

  &__content {
    position: relative;
  }

  &__img {

    width: 1920px;
    height: 850px;
    margin: 0 auto;

    @media screen and (max-width: 1920px) {
      width: 100%;
      height: auto;
    }

    @media screen and (max-width: $breakpoints) {
      width: 622px;
      height: 219px;
      position: absolute;
      bottom: -36px;
      left: 50%;
      transform: translate(-48%, 0);
    }
  }

  &__bg {

    position: absolute;
    top: 0;
    left: 0;
    height: 800px;
    display: flex;

    @media screen and (max-width: 1920px) {
      width: 90%;
      height: auto;
    }

    @media screen and (max-width: $breakpoints) {
      width: auto;
      height: 539px;
      z-index: -1;
    }

    img {
      flex-shrink: 0;
      width: 1653px;
      height: 800px;
      animation: line 30s linear infinite;

      @media screen and (max-width: 1920px) {
        width: 100%;
        height: auto;
      }

      @media screen and (max-width: $breakpoints) {
        width: 1113px;
        height: 539px;
      }
    }
  }

  &__content-block {
    position: absolute;
    z-index: 10;
    top: 0;
    padding-top: 400px;
    left: 50%;
    transform: translate(-50%, 0);

    @media screen and (max-width: 1920px) {
      padding-top: 20%;
    }

    @media screen and (max-width: $breakpoints) {
      padding-top: 193px;
      position: static;
      transform: translate(0, 0);
    }
  }

  &__text-block {
    width: 540px;
    margin: 0 auto;

    @media screen and (max-width: 1450px) {
      width: 370px;
    }

    @media screen and (max-width: $breakpoints) {
      width: 100%;
    }
  }

  &__title {
    padding-bottom: 20px;
    text-align: center;
    font-size: 30px;
    font-weight: bold;

    @media screen and (max-width: $breakpoints) {
      text-align: left;
      padding-bottom: 10px;
    }
  }

}