.logistics {
	padding: 60px 0;
	background: #E2EEFF;
	position: relative;
	overflow: hidden;
	@include r(1140) {
		padding: 80px 0; }
	&__bg {
		position: absolute;
		top: 50px;
		left: 50%;
		transform: translateX(-53%);
		@include r(1140) {
			top: 13px;
			transform: translateX(-71%); } }
	&-content {
		position: relative; }
	&__title {
		margin-bottom: 62px;
		@include r(1140) {
			text-align: left;
			font-size: 28px;
			margin-bottom: 15px; } } }
.map {
	width: 1336px;
	height: 660px;
	@include r(1140) {
		width: 893px;
		height: 438px; }
	img {
		width: 100%; }
	&__point {
		position: absolute;
		transform: scale(0);
		animation: scalePoint 11s infinite;
		@include r(1140) {
			animation-name: scalePointSm; }
		&-1 {
			top: 40%;
			right: 15%;
			animation-delay: 0; }
		&-2 {
			top: 49%;
			right: 25%;
			animation-delay: 8s; }
		&-3 {
			top: 48.5%;
			right: 35%;
			animation-delay: 3s; }
		&-4 {
			bottom: 4.5%;
			right: 44%;
			animation-delay: 2s; }
		&-5 {
			top: 29%;
			left: 18%;
			animation-delay: 10s; }
		&-6 {
			top: 59%;
			left: 26%;
			animation-delay: 8s; }
		&-7 {
			top: 40%;
			left: 47%;
			animation-delay: 4.5s; }
		&-8 {
			top: 35%;
			left: 67%;
			animation-delay: 9s; }
		&-9 {
			top: 35%;
			right: 20%;
			animation-delay: 4s; }
		&-10 {
			bottom: 15%;
			right: 10%;
			animation-delay: 3s; }
		&-11 {
			bottom: 31%;
			right: 17%;
			animation-delay: 5.5s; }
		&-12 {
			bottom: 31%;
			left: 17%;
			animation-delay: 7s; }
		&-13 {
			bottom: 31%;
			left: 17%;
			animation-delay: 1s; }
		&-14 {
			bottom: 31%;
			left: 55%;
			animation-delay: 5s; }
		&-15 {
			top: 12%;
			left: 51%;
			animation-delay: 8s; }
		&-16 {
			bottom: 41%;
			left: 42%;
			animation-delay: 9s; }
		&-17 {
			top: 31%;
			left: 8%;
			animation-delay: 5s; }
		&-18 {
			top: 35%;
			left: 51%;
			animation-delay: 7.5s; }
		&-19 {
			top: 12%;
			left: 10%;
			animation-delay: 6s; }
		&-20 {
			top: 12%;
			right: 20%;
			animation-delay: 2s; } } }

.point {
	width: 20px;
	height: 20px;
	background: #F9B032;
	border-radius: 50%;
	@include r(1140) {
		width: 13px;
		height: 13px; } }

.count-slider {
	display: flex;
	align-items: center;
	justify-content: center;
	.nums {
		height: 42px;
		position: relative;
		width: 100%; }
	.num {
		opacity: 0;
		position: absolute;
		width: 100%;
		top: 0;
		@include r(1140) {
			text-align: center; }
		&.active {
			animation: opacityNum linear; } }
	&-text {
		line-height: 1.175;
		width: 200px;
		z-index: 10;
		@include r(1140) {
			text-align: center;
			margin: 0 0 15px; }
		strong {
			display: block;
			font-size: 18px;
			font-weight: 700; }
		.nums {
			font-size: 40px;
			font-weight: 700; } }
	.img {
		position: absolute;
		opacity: 0;
		// overflow: hidden
		&-line {
			position: absolute;
			bottom: 3px;
			left: 50%;
			transform: translateX(-50%); } } }
.shipping {
	min-height: 104px;
	padding-left: 215px;
	margin-bottom: 25px;
	@include r(1140) {
		padding-left: 0;
		flex-direction: column-reverse;
		margin-bottom: 40px; }
	&-text {
		position: relative;
		margin-left: 40px;
		.num {
			text-align: left;
			left: 0; } }
	&-images {
		position: relative;
		width: 303px;
		height: 104px; }
	.img {
		top: 50%;
		transform: translate(-500px, -50%);
		@include r(1140) {
			right: auto;
			left: 50%;
			transform: translate(-400px, -50%); }
		&.active {
			animation: ImgToRight  linear;
			@include r(1140) {
				animation-name: ImgToRightSm; }
			.img-traffic {
				animation: ImgToRightEl ease-in; } } } }
.delivery {
	min-height: 140px;
	padding-right: 105px;
	margin-bottom: 87px;
	@include r(1140) {
		padding-right: 0;
		flex-direction: column;
		margin-bottom: 60px; }
	&-text {
		position: relative;
		margin-right: 40px;
		text-align: right;
		.num {
			text-align: right;
			right: 0; } }
	&-images {
		position: relative;
		width: 303px;
		height: 140px; }
	.img {
		bottom: 0;
		left: 0;
		transform: translate(500px, 0);
		@include r(1140) {
			left: 50%;
			transform: translate(320px, -50%); }
		&.active {
			animation: ImgToLeft  linear;
			@include r(1140) {
				animation-name: ImgToLeftSm; }
			.img-traffic {
				animation: ImgToLeftEl ease-in; } } } }


.capabilities {
	&__title {
		font-size: 25px;
		line-height: 1.1;
		text-align: center;
		margin-bottom: 25px;
		@include r(1140) {
			text-align: left; } }
	&-list {
		display: flex;
		width: 920px;
		margin: 0 auto;
		justify-content: space-between;
		@include r(1140) {
			display: block; } }
	&__el {
		width: 290px;
		position: relative;
		flex-shrink: 0;
		padding-left: 47px;
		padding-top: 10px;
		font-size: 16px;
		line-height: 1.1875;
		@include r(1140) {
			margin-bottom: 30px; }
		&:last-of-type {
			@include r(1140) {
				margin-bottom: 0; } }
		a {
			text-decoration: underline;
			font-weight: 500; }
		&-2 {
			width: 300px; } }
	.fico {
		font-size: 2.5rem;
		position: absolute;
		left: 0;
		toP: 0; } }

@keyframes scalePoint {
	0%, 15%, 100% {
		transform: scale(0);
		box-shadow: 0 0 0 0 rgba(249,176,50,0.5); }
	7%, 8% {
		transform: scale(1);
		box-shadow: 0 0 0 8px rgba(249,176,50,0.5); } }
@keyframes scalePointSm {
	0%, 15%, 100% {
		transform: scale(0);
		box-shadow: 0 0 0 0 rgba(249,176,50,0.5); }
	7%, 8% {
		transform: scale(1);
		box-shadow: 0 0 0 4px rgba(249,176,50,0.5); } }
@keyframes ImgToRight {
	0% {
		transform: translate(-500px, -50%);
		opacity: 0; }
	10%, 90% {
		transform: translate( 0, -50%);
		opacity: 1; }
	100% {
		transform: translate( 0, -50%);
		opacity: 0; } }
@keyframes ImgToRightSm {
	0% {
		transform: translate(-400px, -50%);
		opacity: 0; }
	10%, 90% {
		transform: translate( -50%, -50%);
		opacity: 1; }
	100% {
		transform: translate( -50%, -50%);
		opacity: 0; } }
@keyframes ImgToLeft {
	0% {
		transform: translate(500px, 0);
		opacity: 0; }
	10%, 90% {
		transform: translate( 0, 0);
		opacity: 1; }
	100% {
		transform: translate( 0, 0);
		opacity: 0; } }
@keyframes ImgToLeftSm {
	0% {
		transform: translate(320px, 0);
		opacity: 0; }
	10%, 90% {
		transform: translate( -50%, 0);
		opacity: 1; }
	100% {
		transform: translate( -50%, 0);
		opacity: 0; } }
@keyframes ImgToRightEl {
	0%, 60% {
		transform: translateX(0);
		opacity: 1; }
	85%, 100% {
		transform: translateX(320px);
		opacity: 0; } }
@keyframes ImgToLeftEl {
	0%, 60% {
		transform: translateX(0);
		opacity: 1; }
	85%, 100% {
		transform: translateX(-220px);
		opacity: 0; } }
@keyframes opacityNum {
	0%, 100% {
		opacity: 0; }
	10%, 90% {
		opacity: 1; } }
