body {
	font-family: 'Roboto', sans-serif;
	font-size: 16px;
	line-height: 1.2;
	color: #072C74;
}

:root {
	--link-width: 141px;
}

// Font icons
.fico {
	display: inline-block;
	font-size: 3rem;

	&::before {
		display: block;
	}
}

// ----------------------------------

* {
	box-sizing: border-box;
}

a {
	text-decoration: none;
	color: #072C74;
}

select:focus,
input:focus,
button:focus {
	outline: none;
}

.container {
	width: 1140px;
	padding-left: 15px;
	padding-right: 15px;
	margin: 0 auto;

	@media screen and (max-width: $breakpoints) {
		width: 320px;
	}
}

.title {
	font-weight: bold;
	font-size: 40px;
	text-align: center;

	@media screen and (max-width: $breakpoints) {
		font-size: 28px;
	}
}

#main-page {
	.title {
		@media screen and (max-width: $breakpoints) {
			text-align: left;
		}
	}
}

.subtitle {
	font-weight: bold;
	font-size: 25px;

}

.text {
	line-height: 1.4;

	p {
		padding-bottom: 10px;
	}
}

.link-hover {
	position: relative;
	overflow: hidden;

	&::after {
		content: '';
		width: 100%;
		position: absolute;
		z-index: 10;
		bottom: 0;
		left: 0;
		transform: translate(-110%, 0);
	}
}



@import "loader";

@import "header";
@import "section1";
