.button {
  font-family: 'Roboto', sans-serif;
  cursor: pointer;
  display: block;
  border: none;
  font-size: 14px;
  line-height: 1;
  background: #FF6E4E;
  color: #FFFFFF;
  text-align: center;
  text-transform: uppercase;
  border-radius: 32px;
  padding: 13px 20px;
  min-width: 183px;
  max-width: 100%;
  margin: 0 auto;
  transition: 0.3s;

  @media screen and (max-width: $breakpoints) {}
}

.button:hover {
  background: #FD8A65;
  transition: 0.3s;
}

.button:active {
  background: #D74213;
  transition: 0.3s;
}

.form-block {
  width: 300px;
  margin: 0 auto;

  @media screen and (max-width: $breakpoints) {
    width: 100%;
  }
}

.form {
  width: 100%;

  &__select,
  &__input {
    font-family: 'Roboto', sans-serif;
    width: 100%;
    height: 40px;
    padding-left: 18px;
    padding-right: 18px;
    background: #FFFFFF;
    border: 1px solid rgba(7, 44, 116, 0.5);
    border-radius: 47px;
    font-size: 14px;
    color: #072C74;
  }

  &__select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

  &__input:focus {
    border: 1px solid #072C74;
  }

  &__input-block:not(:last-child) {
    padding-bottom: 12px;
  }

  &__star {
    color: #FF6E4E;
  }

  &__file {
    display: flex;
    align-items: center;
  }

  &__file-img {
    flex-shrink: 0;
    margin-right: 10px;
  }

  .has-success &__input {
    // border: solid 1px rgba(0, 154, 33, 0.73);
    background-image: url(../img/success.svg) !important;
    background-repeat: no-repeat;
    background-position: right center;
    background-origin: content-box;
    background-size: 22px;
  }

  .has-error &__input {
    border: solid 1px #DD3C3C;
    background-image: url(../img/error.svg) !important;
    background-repeat: no-repeat;
    background-position: right center;
    background-origin: content-box;
    background-size: 20px;
  }

  .has-error .placeholder {
    color: #DD3C3C !important;
  }

  .has-error .checkmark {
    border: solid 1px #DD3C3C !important;
    background-color: #fff !important;
  }

}



.input-wrap {
  position: relative;
}

.file-container {
  position: relative;

  input {
    opacity: 0;
  }

  .file-text {
    pointer-events: none;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
    font-size: 14px;
    color: rgba($color: #072C74, $alpha: 0.7);
    text-decoration-line: underline;

  }
}

.placeholder-container {
  position: relative;

  .placeholder {
    pointer-events: none;
    position: absolute;
    left: 16px;
    top: 50%;
    transform: translate(0, -50%);
    padding: 0 2px;
    font-size: 14px;
    color: rgba($color: #072C74, $alpha: 0.7);
    background: #FFFFFF;
  }

  input.active+.placeholder {
    color: #072C74;
    font-size: 10px;
    top: -4px;
    transform: translate(0, 0);
  }
}

/* Customize the checkbox */
.checkbox-container {
  display: block;
  cursor: pointer;
  user-select: none;
  // overflow: hidden;

  &_password {
    position: absolute;
    right: 18px;
    top: 50%;
    transform: translate(0, -50%);
    width: 24px;
    height: 24px;
  }

  &_registration {
    flex-shrink: 0;
    position: relative;
    width: 19px;
    height: 19px;
    margin-right: 11px;
  }

  /* Hide the browser's default checkbox */
  input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    display: block;
    height: 100%;
    width: 100%;
    margin: 0;
  }
}

.help-block {
  position: absolute;
  left: 0;
  top: -50px;
  min-width: 300px;
  background: #FFFFFF;
  border-radius: 20px;
  height: 40px;
  padding-left: 18px;
  padding-right: 18px;
  border: solid 1px #DD3C3C;
  font-size: 14px;
  line-height: 1;
  color: #DD3C3C;
  display: none;
  align-items: center;
}

.help-block::after {
  content: url(../img/img1.png);
  position: absolute;
  left: 20px;
  bottom: -14px;
}

.has-error .help-block {
  display: flex;
}

/* Create a custom checkbox */
.checkmark {
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  height: 100%;
  width: 100%;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  position: absolute;
}

.checkbox-container_password .checkmark:after {
  width: 22px;
  left: 0;
}

.checkbox-container_registration .checkmark:after {
  width: 22px;
  left: 2px;
}

/* Show the checkmark when checked */
.checkbox-container_password input:checked~.checkmark:after {
  content: url(../img/password2.svg);
  top: 2px;
}

/* Style the checkmark/indicator */
.checkbox-container_password .checkmark:after {
  content: url(../img/password1.svg);
  top: 5px;
}

/* Show the checkmark when checked */
.checkbox-container_registration input:checked~.checkmark {
  background: #FFFFFF;
}

.checkbox-container_registration input:checked~.checkmark:after {
  content: url(../img/checkmark.svg);
  top: -1px;
}

/* Style the checkmark/indicator */
.checkbox-container_registration .checkmark {
  background: rgba(7, 44, 116, 0.2);
  border: 1px solid rgba(7, 44, 116, 0.5);
  border-radius: 4px;
}

.checkbox-container_registration .checkmark:after {
  content: '';
}

.chosen-select {
  height: 40px;
}

// chosen

.form {
  .chosen-container {
    .chosen-single {
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      color: #072c74;
      width: 100%;
      height: 40px;
      padding-left: 18px;
      padding-right: 18px;
      background: #FFFFFF;
      border: 1px solid rgba(7, 44, 116, 0.5);
      border-radius: 20px;
      box-shadow: none;
      line-height: 40px;
    }

    .chosen-single.chosen-default {
      color: rgba(7, 44, 116, .7);
    }

    .chosen-drop {
      box-shadow: none;
      border: 1px solid rgba(7, 44, 116, 1);
      border-radius: 0 0 20px 20px;
      border-top: 1px solid #fff;
    }

    .chosen-drop::before {
      display: block;
      padding: 0 8px;
      content: '';
      height: 1px;
      background-color: #E2EEFF;
      background-clip: content-box;
    }

    .chosen-results {
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      color: #072c74;
      margin: 7px 15px 18px 0;
      padding: 0 0 0 12px;
    }
  }

  .chosen-container-active {
    .chosen-single {
      border: 1px solid rgba(7, 44, 116, 1);
    }
  }

  .chosen-container.chosen-with-drop {
    .chosen-single {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;

    }
  }
}

.chosen-results::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1);
  background-color: rgba(0, 0, 0, 0);
}

.chosen-results::-webkit-scrollbar {
  width: 3px;
  background-color: rgba(0, 0, 0, 0);
}

.chosen-results::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}


