.section5 {
  background-color: #fff;
  overflow: hidden;
  padding-bottom: 60px;
  background-image: url(../img/s5-bg.svg);
  background-repeat: no-repeat;
  background-position: center bottom;
	@media screen and (max-width: $breakpoints) {
    background-image: none;
    padding-bottom: 0;
  }
  &__content {
    padding-top: 55px;
    position: relative;
    @media screen and (max-width: $breakpoints) {
      padding-top: 75px;
    }
  }
  &__img-block1 {
    position: absolute;
    bottom: 0;
    left: -200px;
    @media screen and (max-width: $breakpoints) {
      position: relative;
      left: 0;
      height: 235px;
     
    }
  }
  &__img-block2 {
    position: absolute;
    bottom: 0;
    right: -260px;
    @media screen and (max-width: $breakpoints) {
     position: relative;
     right: 0;
     height: 200px;
    
    }
  }
  &__img1 {
   
    width: 587px;
    height: 300px;
    @media screen and (max-width: $breakpoints) {
      width: 438px;
      height: 235px;
      position: absolute;
      top: 0;
     left: -45px;
    }
  }
  &__img2 {
   
    width: 587px;
    height: 300px;
    @media screen and (max-width: $breakpoints) {
      width: 363px;
      height: 200px;
      position: absolute;
      top: 0;
      left: -52px;
    }
  }
  &__text-block {
    position: relative;
    z-index: 1;
    width: 540px;
    margin: 0 auto;
    min-height: 400px;
    @media screen and (max-width: $breakpoints) {
      width: 100%;
      min-height: 1px;
      padding-top: 40px;
      padding-bottom: 30px;

    }
  }
  &__title {
    padding-bottom: 20px;
    @media screen and (max-width: $breakpoints) {
      padding-bottom: 10px;
    }
  }
  &__text {
    @media screen and (max-width: $breakpoints) {
      line-height: 1.2;
    }
  }
 
}