@keyframes menu {
  0% {
    transform: translate(-100%, 0);
  }

  1% {
    transform: translate(0, 0);
  }

  20% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(100%, 0);
  }

  51% {
    transform: translate(-100%, 0);
  }

  100% {
    transform: translate(0, 0);
  }

}

@keyframes line {
  0% {
    transform: translate3d(0, 0, 0);
  }

  100% {
    transform: translate3d(-100%, 0, 0);
  }
}

@keyframes move {
  0% {
    transform: translate3d(0, 0, 0);
  }

  50% {
    transform: translate3d(0, 5px, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}
